// 在组件状态中添加
const [selectedCategory, setSelectedCategory] = useState<string>('all');

// 添加分类选择处理函数
const handleCategorySelect = (category: string) => {
  setSelectedCategory(category);
  
  // 根据选择的分类重新生成范围
  const newRanges = generateLevelRanges(category);
  if (newRanges.length > 0) {
    setSelectedRange(newRanges[0]);
    handleRangeSelect(newRanges[0], category);
  } else {
    setLevelsInRange([]);
  }
};

// 修改generateLevelRanges函数以接受分类参数
const generateLevelRanges = (category: string): LevelRange[] => {
  const ranges: LevelRange[] = [];
  
  // 根据分类筛选关卡
  const filteredLevels = category === 'all' 
    ? levelsData 
    : levelsData.filter(level => level.category === category);
  
  // 获取该分类下的最大关卡ID
  const maxLevel = filteredLevels.length > 0 
    ? Math.max(...filteredLevels.map(level => level.id))
    : 0;
  
  if (maxLevel === 0) return ranges;
  
  // 创建分段，每30个关卡为一组（与图片中的设计一致）
  for (let start = 1; start <= maxLevel; start += 30) {
    const end = Math.min(start + 29, maxLevel);
    ranges.push({
      start,
      end,
      label: `Level ${start}-${end}`
    });
  }
  
  return ranges;
};

// 修改handleRangeSelect函数以接受分类参数
const handleRangeSelect = (range: LevelRange, category?: string) => {
  const currentCategory = category || selectedCategory;
  setSelectedRange(range);
  setLoading(true);
  
  // 从导入的数据中筛选当前分类和范围内的关卡
  const levelsInSelectedRange = levelsData.filter(level => 
    level.id >= range.start && 
    level.id <= range.end &&
    (currentCategory === 'all' || level.category === currentCategory)
  );
  
  setLevelsInRange(levelsInSelectedRange);
  setLoading(false);
};